import { Block } from '@ali/4ever-cangjie';

export type BlockHoverMsg = {
  isHover: boolean;
  block: Block;
};

type Callback = (msg: BlockHoverMsg) => void;

let callbacks: Callback[] = [];

function on(c: Callback) {
  callbacks.push(c);
}

function off(c: Callback) {
  callbacks = callbacks.filter((cb) => cb !== c);
}

function trigger(msg: BlockHoverMsg) {
  callbacks.forEach((c) => c(msg));
}

export default {
  on,
  off,
  trigger,
};
